main {
  margin: 0 auto;
  max-width: 90%;
  padding: 50px 30px;
}

:root {
  --primary: #499167;
  --secondary: #2E282A;
  --tertiary: #f0f2a6;
  --quaternary: #39a9db;
  --quinary: #bfd7ea;
  --light: #bfd7ea;
  --dark: #353831;
  --darker: #39a9db;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);
}

* {
  box-sizing: border-box;
}

html, body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: .1em;
  background: var(--dark);
  color: var(--light);
  font-family: 'Courier New', Courier, monospace;
}

h1, h2, h3, h4, h5, h6 {
  font-family: sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: red;
  text-decoration: none;
  font-weight: bold;
  border-radius: 5px;
  font-weight: 600;
  padding: 2px 4px;
  margin-left: -2px;
  margin-right: -2px;
}

a:hover {
  text-decoration: underline;
}

header {
  background: var(--secondary);
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  text-align: center;
}

header a {
  color: var(--light);
}

button {
  background: var(--quaternary);
  border: 0;
  border-radius: .5rem;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  padding: 10px 15px;
  font-size: 1rem;
}

button:hover {
  opacity: .8;
}

button[disabled] {
  cursor: default;
  opacity: .8;
}

img {
  max-width: 100%;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
  width: 600px;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

ol li {
  list-style: inside;
}

ul li {
  list-style: none;
}

nav a:hover {
  text-decoration: none;
}

nav li {
  cursor: pointer;
}

textarea {
  width: 600px;
  height: 120px;
  border: 3px solid #cccccc;
  padding: 5px;
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.img-thumbnail {
  width: auto;
  height: 300px;
  margin-bottom: 50px;
  cursor: pointer;
}

.navActive {
  color: var(--primary);
}

.footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.photo {
  width: auto;
  height: auto;
  max-width: 1000px;
  max-height: 1000px;
  margin-bottom: 50px;
}

.image {
  width: auto;
  height: auto;
  max-width: 500px;
  max-height: 500px;
  padding: 50px;
}

.logo {
  width: 50px;
  height: 50px;
  margin-bottom: 50px;
  cursor: pointer;
}

.logo:hover {
  opacity: 0.5;
}

.center {
  display: flex;
  justify-content: center;
}

.page-header {
  font-family: sans-serif;
  font-size: 2.5em;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
  padding-bottom: 2rem;
}

.bottom-spacing {
  margin-bottom: var(--spacing-five);
}

.card {
  width: 25%;
  text-align: center;
}

.card-title {
  font-size: 1.5rem;
  color: var(--quaternary);
}

.card-subtitle {
  font-size: 1rem;
  color: var(--tertiary);
}

.card-text {
  font-size: 0.75rem;
  height: 8rem;
  color: var(--quinary);
}

.card-techs {
  font-size: 0.75rem;
  height: 4rem;
  color: var(--quinary);
}

.card-image {
  max-width: 300px;
  max-height: 150px;
}

.card-link {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 0.70rem;
}

.padding {
  padding: 50px;
}